import React from 'react';

const BlogRoom = () => {
  return (
    <div className="flex items-center justify-between w-full max-w-4xl bg-yellow-600 bg-opacity-75 p-4 md:p-6 rounded-lg shadow-lg">
      {/* Blog Room Text */}
      <div className="flex-shrink-0 mr-4">
        <h2 className="text-green-800 text-2xl md:text-3xl font-bold uppercase tracking-wide">
          Blog Room
        </h2>
      </div>

      {/* Navigation Arrows (Left) - Hidden on extra small screens */}
      <div className="hidden sm:flex items-center justify-center text-white text-4xl mr-4 cursor-pointer hover:text-gray-200 transition-colors duration-200">
        &#9664; {/* Left arrow Unicode character */}
      </div>

      {/* Blog Post Title */}
      <div className="flex-grow text-white text-sm md:text-base text-center sm:text-left mx-2 md:mx-4">
        <p className="font-semibold leading-tight">
          FROM HORMONES TO HEART HEALTH: EXPERT PHARMACEUTICAL CARE FOR EVERY STAGE OF LIFE
        </p>
      </div>

      {/* Read More Button */}
      <div className="flex-shrink-0 ml-4">
        <button className="bg-green-700 hover:bg-green-800 text-white font-bold py-2 px-4 md:py-3 md:px-6 rounded-full shadow-md transition duration-300 ease-in-out">
          Read More
        </button>
      </div>

      {/* Navigation Arrows (Right) - Hidden on extra small screens */}
      <div className="hidden sm:flex items-center justify-center text-white text-4xl ml-4 cursor-pointer hover:text-gray-200 transition-colors duration-200">
        &#9654; {/* Right arrow Unicode character */}
      </div>
    </div>
  );
};

export default BlogRoom;